<style lang="less" scoped>
  .newhome{
    width: 100%;
    padding-top: 10px;
    ul{
      list-style: none;
      padding: 0;
      margin: 0;
    }
    .n-navMenu{
      display: flex;
      .navOne{
        flex: 1;
        text-align: center;
        height: 40px;
        color: white;
        line-height: 40px;
        background-color: #011753;
        cursor: pointer;
        ul{
          display: none;
        }
      }
      .navOne:hover{
        background-color: #153697;
        position: relative;
        ul{
          z-index: 100;
          display: block;
          position: absolute;
          width: 100%;
          background: yellow;
          top: 40px;
          background: linear-gradient(#153697, #8DA4E8); /*  */
          .navTwo:hover{
            background-color: #011753;
            cursor: pointer;
          }
        }
      }
    }
    .ggImg{
      height: 300px;
      position: absolute;
      width: 100%;
      min-width: 1200px;
      left: 0;
      display: block;
      img{
        height: 100%;
        width: 100%;
        position: relative;
      }
    }
    .special{
      margin-top: 300px;
      background: white;
      display: flex;
      padding: 10px;
      .special-left{
        background-color: #A90013;
        width: 40px;
        word-wrap: break-word;
        letter-spacing:5px;
        text-align: center;
        color: white;
        padding: 50px 0;
      }
      .special-right{
        flex: 1;
        overflow: hidden;
        .swiper-inner {
          .swiper-content{
            padding: 0 10px;
            p{
              padding: 0;
              margin: 0;
              font-size: 12px;
            }
            .p1{
              font-size: 14px;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
              padding: 5px 0;
              font-weight: 600;
            }
            .p2{
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            img{
              width: 100%;
              height: 150px;
            }
          }
        }
      }
    }
    .Journalism-view{
      display: flex;
      padding-bottom: 20px;
      .list-view{
        flex: 1;
        padding-right: 20px;
        padding-left: 5px;
        overflow: hidden;
        .header-title{
          color: #A90013;
          border-left: 2px solid #A90013;
          text-indent: 5px;
          font-weight: 600;
          margin-bottom: 0;
          .gd{
            float: right;
            color: black;
            font-size: 12px;
            cursor: pointer;
            text-decoration: none;
          }
        }
        ul{
          margin-left: 10px;
          font-size: 14px;
          color: #7C7C7C;
          width: 100%;
          li{
            width: 100%;
            padding: 8px 0;
            border-top: 1px solid #eeeeee;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            cursor: pointer;
          }
          li:hover{
            color: #A90013;
          }
        }
        table{
          width: 100%;
          table-layout: fixed;
          margin-top: 20px;
          tr{
            width: 100%;
            td{
              text-align: center;
              a{
                color: white;
                text-decoration: none;
                padding: 10px 0;
                display: block;
              }
            }
          }
        }
      }
    }
  }
</style>



